* {
    box-sizing: border-box;
    user-select: none;
    /*font-family: roboto;*/
}

body {
    background-color: aliceblue;
}

.game {
    padding: 10px;   
}

.battle {
    display: flex;
    position: relative;
}

.spell-book {
    position: absolute;
    top: 5px;
    left: 5px;
    width: 752px;
    height: 552px;
    background-color: rgb(234, 248, 252);
    z-index: 10;
}

.battle--field {
    min-width: 762px;
    width: 762px;
    height: 562px;
    background-color: rgb(244, 232, 96);
    padding: 5px;
    border: 1px black solid;
}

.battle--field--square {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    height: 28px;
    width: 28px;
    margin: 0px 0px -4px 0px;
    border: 1px gray solid;
    cursor: pointer;
    user-select: none;
}

.highlight {
    background-color: lightgreen !important;
}

.btMapCreature {
    width: 28px;
    height: 28px;
    position: absolute;
}

.battle--menu {
    width: 411px;
    background-color: rgb(212, 250, 255);
    padding: 5px;
    margin-left: 10px;
    border: 1px black solid;
}

.battle--menu--buttons-block {
    height: 50px;
    margin-bottom: 5px;
}

.battle--menu--button {
    height: 48px;
    margin-right: 5px;
    cursor: pointer;
}

.battle--menu--btnDesc {
    display: inline-flex;
    align-items: center;
    font-size: 0.9em;
    width: 100px;
    height: 48px;
}

.battle--button:hover {
    background-color: rgb(209, 196, 131);
}

.battle--creature-info {
    display: flex;
    flex-direction: column;
    width: 100%;
    border: 1px black solid;
}

table {
    width: 100%;
    padding-bottom: 5px;
}

tr {
	text-align: center;  
}

th {
	font-weight: normal;
    background-color: rgb(239, 228, 218);
}

td {
    background-color: rgb(197, 227, 227);
}

.battle--creature-info--img {
    width: 64px;
    height: 64px;
}

.battle--creature-info--logo {
    width: 20px;
    height: 20px;
}

.battle--queue {
    background-color: rgb(217, 233, 248);
    padding: 5px 3px 3px 5px;
    margin-top: 5px;
    border: 1px black solid;
}

.battle--queue--img {
    width: 48px;
    height: 48px;
    background-color: rgb(165, 207, 207);
    margin: 0 2px 2px 0;
    border: 2px rgb(165, 207, 207) solid;
    cursor: pointer;
}
.battle--queue--img:hover {
    background-color: rgb(108, 248, 113);
    border: 2px green solid;
}

.skill-bar {
    width: 410px;
    margin-top: 10px;
}

.skill-bar--item {
    position: relative;
    display: inline-block;
    width: 40px;
    height: 40px;
    cursor: pointer;
}

.skill-bar--tooltip {
    position: absolute;
    bottom: 50px;
    left: -150px;
    color: white;
    width: 280px;
    background-color: rgb(29, 35, 34);
    border: 30px solid;
    border-image: url("img/blueframe.png") 50 50 50 50 repeat;
}

.tooltip--content {
    margin: -15px -5px -5px -5px;
}

.tooltip--header {
    position: relative;
    display: flex;
    align-items: center;
    height: 40px;
    margin-bottom: 5px;
}

.tooltip--header-name {
    font-size: 1.1em;
    font-weight: bold;
}

.tooltip--header-image {
    position: absolute;
    right: -10px;
    width: 40px;
    height: 40px;
}

.tooltip--description {
    font-style: italic;
    margin-top: 10px;
}

.spell-effects {
    margin-top: 15px;
}

.spell-effects--item {
    position: relative;
    display: inline-block;
    width: 32px;
    height: 32px;
    margin: 0 5px 5px 0;
    cursor: pointer;
}

.spell-effects--tooltip {
    position: absolute;
    bottom: 50px;
    left: -100px;
    color: white;
    width: 220px;
    background-color: rgb(29, 35, 34);
    border: 30px solid;
    border-image: url("img/blueframe.png") 50 50 50 50 repeat;
}

.buff-effect {
    display: block;
    color: lightgreen;
}

.debuff-effect {
    display: block;
    color: red;
}




/*КОНЕЦ БИТВЫ*/
.GG {
    position: fixed;
    top: 50%;
    left: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 30px;
    background-color: rgb(136, 245, 136);
    color: black;
    width: 100px;
    height: 100px;
    border: 10px black solid;
    transform: translate(-50%, -50%);
}
